const validasiTolakanPembayaranHutangSupplier = (value) => {
    const errors = {};
    if(!value.tanggal){
        errors.tanggal = "Tanggal Tidak Boleh Kosong"
    }
    if(!value.supplier){
        errors.supplier = "Tanggal Tidak Boleh Kosong"
    }
    if(!value.noBon){
        errors.noBon = "Tanggal Tidak Boleh Kosong"
    }
    return errors;
}

export default validasiTolakanPembayaranHutangSupplier;